import React, { useState, useEffect } from 'react';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '../ui/card.jsx';
import { Button } from '../ui/button.jsx';
import { Switch } from '../ui/switch.jsx';
import { Input } from '../ui/input.jsx';
import { Badge } from '../ui/badge.jsx';
import { Skeleton } from '../ui/skeleton.jsx';
import { Loader2, Search, CheckCircle2, XCircle, Globe, FileText, Archive, AlertCircle, CheckCheck, ChevronLeft, ChevronRight } from 'lucide-react';
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
} from '../ui/alert-dialog.jsx';
import { toast, Toaster } from '../ui/toast.jsx';

const __ = (text, domain) => {
  if (typeof window !== 'undefined' && window.wp && window.wp.i18n) {
    return window.wp.i18n.__(text, domain);
  }
  return text;
};

const sprintf = (format, ...args) => {
  if (typeof window !== 'undefined' && window.wp && window.wp.i18n && window.wp.i18n.sprintf) {
    return window.wp.i18n.sprintf(format, ...args);
  }
  return format.replace(/%[sdj%]/g, (match) => {
    if (match === '%%') return '%';
    const arg = args.shift();
    return match === '%s' ? String(arg) : match === '%d' ? Number(arg) : JSON.stringify(arg);
  });
};
import apiGateway from '../../api/gateway.js';

const SyncedMonitor = window.sureFeedbackAdmin?.connection?.sync_saved_locally_image || '';
const SyncedMonitorOff = window.sureFeedbackAdmin?.connection?.sync_saved_locally_off_image || '';

const WidgetControl = () => {
  const [pages, setPages] = useState([]);
  const [settings, setSettings] = useState({});
  const [loading, setLoading] = useState(true);
  const [saving, setSaving] = useState(false);
  const [searchTerm, setSearchTerm] = useState('');
  const [hasUnsavedChanges, setHasUnsavedChanges] = useState(false);
  const [filterType, setFilterType] = useState('all');
  const [currentPage, setCurrentPage] = useState(1);
  const [showEnableAllDialog, setShowEnableAllDialog] = useState(false);
  const [showDisableAllDialog, setShowDisableAllDialog] = useState(false);
  const itemsPerPage = 4;

  const loadPagesSettings = async () => {
    try {
      setLoading(true);
      const response = await apiGateway.get('page-settings');

      if (response.success) {
        setPages(response.pages || []);
        setSettings(response.settings || {});
      }
    } catch (error) {
      toast.error(__('Failed to load page settings', 'surefeedback'));
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    loadPagesSettings();
  }, []);

  const handleToggleChange = (pageId, enabled) => {
    setSettings((prev) => ({
      ...prev,
      [pageId]: enabled,
    }));
    setHasUnsavedChanges(true);
  };

  const saveSettings = async () => {
    try {
      setSaving(true);
      const response = await apiGateway.post('page-settings', {
        settings: settings,
      });

      if (response.success) {
        toast.success(__('Page settings saved successfully', 'surefeedback'));
        setHasUnsavedChanges(false);
        await loadPagesSettings();
      }
    } catch (error) {
      toast.error(__('Failed to save settings', 'surefeedback'));
    } finally {
      setSaving(false);
    }
  };

  const handleEnableAllClick = () => {
    setShowEnableAllDialog(true);
  };

  const confirmEnableAll = async () => {
    try {
      setSaving(true);
      const response = await apiGateway.post('page-settings/enable-all');

      if (response.success) {
        toast.success(__('Widget enabled for all pages', 'surefeedback'));
        setHasUnsavedChanges(false);
        await loadPagesSettings();
      }
    } catch (error) {
      toast.error(__('Failed to enable widget for all pages', 'surefeedback'));
    } finally {
      setSaving(false);
      setShowEnableAllDialog(false);
    }
  };

  const handleDisableAllClick = () => {
    setShowDisableAllDialog(true);
  };

  const confirmDisableAll = async () => {
    try {
      setSaving(true);
      const response = await apiGateway.post('page-settings/disable-all');

      if (response.success) {
        toast.success(__('Widget disabled for all pages', 'surefeedback'));
        setHasUnsavedChanges(false);
        await loadPagesSettings();
      }
    } catch (error) {
      toast.error(__('Failed to disable widget for all pages', 'surefeedback'));
    } finally {
      setSaving(false);
      setShowDisableAllDialog(false);
    }
  };

  const getPageIcon = (type) => {
    switch (type) {
      case 'home':
      case 'blog':
        return <Globe className="h-4 w-4 text-primary" />;
      case 'page':
      case 'post':
        return <FileText className="h-4 w-4 text-primary" />;
      case 'archive':
      case 'search':
      case '404':
        return <Archive className="h-4 w-4 text-primary" />;
      default:
        return <FileText className="h-4 w-4 text-primary" />;
    }
  };

  const getPageTypeBadge = (type, typeLabel) => {
    const displayText = (typeLabel || type).toLowerCase();
    return (
      <Badge
        variant="outline"
        className="text-xs font-normal text-gray-900 border-gray-300 bg-white px-2 py-0.5 rounded-full"
      >
        {displayText}
      </Badge>
    );
  };

  const isPageEnabled = (pageId) => {
    if (Object.keys(settings).length === 0) {
      return true;
    }
    return settings[pageId] !== false;
  };

  const filteredPages = pages.filter((page) => {
    const matchesSearch = page.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
      page.type.toLowerCase().includes(searchTerm.toLowerCase());

    if (filterType === 'all') return matchesSearch;
    if (filterType === 'enabled') return matchesSearch && isPageEnabled(page.id);
    if (filterType === 'disabled') return matchesSearch && !isPageEnabled(page.id);

    return matchesSearch && page.type === filterType;
  });

  const totalPages = Math.ceil(filteredPages.length / itemsPerPage);
  const startIndex = (currentPage - 1) * itemsPerPage;
  const endIndex = startIndex + itemsPerPage;
  const paginatedPages = searchTerm ? filteredPages : filteredPages.slice(startIndex, endIndex);

  useEffect(() => {
    setCurrentPage(1);
  }, [searchTerm, filterType]);

  const enabledCount = pages.filter((p) => isPageEnabled(p.id)).length;
  const disabledCount = pages.length - enabledCount;

  if (loading) {
    return (
      <div className="space-y-6">
        <div>
          <Skeleton className="h-8 w-40 mb-2 border-0" />
          <Skeleton className="h-4 w-96 border-0" />
        </div>

        <div className="grid gap-4 md:grid-cols-3">
          {[1, 2, 3].map((i) => (
            <Card key={i} className="border-0 shadow-sm !border-0">
              <CardContent className="p-6">
                <div className="flex items-center justify-between">
                  <div className="space-y-2">
                    <Skeleton className="h-4 w-24 border-0" />
                    <Skeleton className="h-7 w-16 border-0" />
                  </div>
                  <Skeleton className="h-10 w-10 rounded-full border-0" />
                </div>
              </CardContent>
            </Card>
          ))}
        </div>

        <Card className="border-0 shadow-sm !border-0">
          <CardHeader className="pb-4">
            <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-4">
              <div className="space-y-2">
                <Skeleton className="h-6 w-32 border-0" />
                <Skeleton className="h-4 w-64 border-0" />
              </div>
              <Skeleton className="h-9 w-32 border-0" />
            </div>
          </CardHeader>
          <CardContent>
            <div className="flex flex-col sm:flex-row gap-3 mb-6">
              <Skeleton className="h-10 flex-1 border-0" />
              <Skeleton className="h-10 w-32 border-0" />
            </div>

            <div className="divide-y divide-gray-200 rounded-lg">
              {[1, 2, 3, 4].map((i) => (
                <div
                  key={i}
                  className="flex items-center justify-between gap-4 p-4 first:rounded-t-lg last:rounded-b-lg"
                >
                  <div className="flex items-center gap-3 flex-1 min-w-0">
                    <Skeleton className="h-8 w-8 rounded-md border-0" />
                    <div className="flex-1 min-w-0 space-y-2">
                      <Skeleton className="h-4 w-48 border-0" />
                      <Skeleton className="h-3 w-full max-w-xs border-0" />
                    </div>
                  </div>
                  <div className="flex items-center gap-3 shrink-0">
                    <Skeleton className="h-5 w-16 rounded-full border-0" />
                    <Skeleton className="h-6 w-11 rounded-full border-0" />
                  </div>
                </div>
              ))}
            </div>

            <div className="flex items-center justify-between mt-6 pt-4">
              <Skeleton className="h-4 w-32 border-0" />
              <div className="flex items-center gap-2">
                <Skeleton className="h-9 w-20 border-0" />
                <Skeleton className="h-9 w-8 border-0" />
                <Skeleton className="h-9 w-8 border-0" />
                <Skeleton className="h-9 w-8 border-0" />
                <Skeleton className="h-9 w-20 border-0" />
              </div>
            </div>
          </CardContent>
        </Card>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      <div>
        <h2 className="text-2xl font-semibold tracking-tight text-gray-900">{__('Widget Control', 'surefeedback')}</h2>
        <p className="text-sm text-gray-600 mt-1.5">
          {__('Control which pages display the SureFeedback widget', 'surefeedback')}
        </p>
      </div>

      <div className="grid gap-4 md:grid-cols-3">
        <Card className="border-0 shadow-sm">
          <CardContent className="p-6">
            <div className="flex items-center justify-between">
              <div className="space-y-1">
                <CardDescription className="text-xs font-medium text-gray-600">{__('Total Pages', 'surefeedback')}</CardDescription>
                <div className="text-2xl font-bold text-gray-900">{pages.length}</div>
              </div>
              <div className="flex h-12 w-12 items-center justify-center rounded-lg bg-primary/10">
                <Globe className="h-6 w-6 text-primary" />
              </div>
            </div>
          </CardContent>
        </Card>

        <Card className="border-0 shadow-sm">
          <CardContent className="p-6">
            <div className="flex items-center justify-between">
              <div className="space-y-1">
                <CardDescription className="text-xs font-medium text-gray-600">{__('Enabled Pages', 'surefeedback')}</CardDescription>
                <div className="text-2xl font-bold text-primary">{enabledCount}</div>
              </div>
              <div className="flex h-12 w-12 items-center justify-center rounded-lg bg-primary/10">
                {SyncedMonitor ? (
                  <img src={SyncedMonitor} alt={__('synced', 'surefeedback')} className="w-6 h-6" />
                ) : (
                  <CheckCircle2 className="h-6 w-6 text-primary" />
                )}
              </div>
            </div>
          </CardContent>
        </Card>

        <Card className="border-0 shadow-sm">
          <CardContent className="p-6">
            <div className="flex items-center justify-between">
              <div className="space-y-1">
                <CardDescription className="text-xs font-medium text-gray-600">{__('Disabled Pages', 'surefeedback')}</CardDescription>
                <div className="text-2xl font-bold text-gray-600">{disabledCount}</div>
              </div>
              <div className="flex h-12 w-12 items-center justify-center rounded-lg bg-gray-100">
                {SyncedMonitorOff ? (
                  <img src={SyncedMonitorOff} alt={__('off - synced', 'surefeedback')} className="w-6 h-6" />
                ) : (
                  <XCircle className="h-6 w-6 text-gray-500" />
                )}
              </div>
            </div>
          </CardContent>
        </Card>
      </div>

      <Card className="border-0 shadow-sm">
        <CardHeader className="pb-4">
          <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-4">
            <div>
              <CardTitle className="font-semibold text-gray-900">{__('Page List', 'surefeedback')}</CardTitle>
              <CardDescription className="mt-1 text-gray-600">
                {__('Toggle widget visibility for individual pages', 'surefeedback')}
              </CardDescription>
            </div>
            <div className="flex gap-2">
              {enabledCount === pages.length ? (
                <Button
                  onClick={handleDisableAllClick}
                  disabled={saving}
                  variant="outline"
                  size="sm"
                >
                  {saving ? (
                    <Loader2 className="h-4 w-4 mr-2 animate-spin" />
                  ) : (
                    <XCircle className="h-4 w-4 mr-2" />
                  )}
                  {__('Disable All', 'surefeedback')}
                </Button>
              ) : (
                <Button
                  onClick={handleEnableAllClick}
                  disabled={saving}
                  size="sm"
                  className="bg-primary hover:bg-primary/90"
                >
                  {saving ? (
                    <Loader2 className="h-4 w-4 mr-2 animate-spin" />
                  ) : (
                    <CheckCheck className="h-4 w-4 mr-2" />
                  )}
                  {__('Enable All', 'surefeedback')}
                </Button>
              )}
            </div>
          </div>
        </CardHeader>
        <CardContent>
          <div className="flex flex-col sm:flex-row gap-3 mb-6">
            <div className="flex-1 relative">
              <Search className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-muted-foreground" />
              <Input
                placeholder={__('Search pages...', 'surefeedback')}
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="pl-10"
              />
            </div>
            <select
              value={filterType}
              onChange={(e) => setFilterType(e.target.value)}
              className="px-3 py-2 text-sm border rounded-md bg-background hover:bg-accent focus:outline-none focus:ring-2 focus:ring-ring focus:ring-offset-2 h-10"
            >
              <option value="all">{__('All Types', 'surefeedback')}</option>
              <option value="enabled">{__('Enabled Only', 'surefeedback')}</option>
              <option value="disabled">{__('Disabled Only', 'surefeedback')}</option>
              <option value="home">{__('Homepage', 'surefeedback')}</option>
              <option value="page">{__('Pages', 'surefeedback')}</option>
              <option value="post">{__('Posts', 'surefeedback')}</option>
              <option value="archive">{__('Archives', 'surefeedback')}</option>
            </select>
          </div>

          <div className="space-y-2">
            {paginatedPages.length === 0 ? (
              <div className="text-center py-12">
                <AlertCircle className="h-10 w-10 mx-auto mb-3 text-muted-foreground" />
                <p className="text-sm text-muted-foreground">{__('No pages found', 'surefeedback')}</p>
              </div>
            ) : (
              <div className="divide-y divide-gray-200 border border-gray-200 rounded-lg">
                {paginatedPages.map((page) => (
                  <div
                    key={page.id}
                    className="flex items-center justify-between gap-4 p-4 hover:bg-gray-50/50 transition-colors first:rounded-t-lg last:rounded-b-lg"
                  >
                    <div className="flex items-center gap-3 flex-1 min-w-0">
                      <div className="flex h-8 w-8 items-center justify-center rounded-md bg-primary/10">
                        {getPageIcon(page.type)}
                      </div>
                      <div className="flex-1 min-w-0">
                        <div className="font-medium text-sm text-gray-900 truncate">{page.title}</div>
                        {page.url && (
                          <div className="text-xs text-gray-600 truncate mt-0.5">
                            {page.url}
                          </div>
                        )}
                      </div>
                    </div>
                    <div className="flex items-center gap-3 shrink-0">
                      {getPageTypeBadge(page.type, page.type_label)}
                      <Switch
                        checked={isPageEnabled(page.id)}
                        onCheckedChange={(checked) => handleToggleChange(page.id, checked)}
                      />
                    </div>
                  </div>
                ))}
              </div>
            )}
          </div>

          {!searchTerm && totalPages > 1 && (
            <div className="flex items-center justify-between border-t border-gray-200 pt-4">
              <div className="text-sm text-gray-600">
                {sprintf(__('Showing %1$d-%2$d of %3$d pages', 'surefeedback'), startIndex + 1, Math.min(endIndex, filteredPages.length), filteredPages.length)}
              </div>
              <div className="flex items-center gap-2">
                <Button
                  variant="outline"
                  size="sm"
                  onClick={() => setCurrentPage(prev => Math.max(1, prev - 1))}
                  disabled={currentPage === 1}
                >
                  <ChevronLeft className="h-4 w-4 mr-1" />
                  {__('Previous', 'surefeedback')}
                </Button>
                <div className="flex items-center gap-1">
                  {Array.from({ length: Math.min(totalPages, 5) }, (_, i) => {
                    let pageNum;
                    if (totalPages <= 5) {
                      pageNum = i + 1;
                    } else if (currentPage <= 3) {
                      pageNum = i + 1;
                    } else if (currentPage >= totalPages - 2) {
                      pageNum = totalPages - 4 + i;
                    } else {
                      pageNum = currentPage - 2 + i;
                    }
                    return (
                      <Button
                        key={pageNum}
                        variant={currentPage === pageNum ? "default" : "outline"}
                        size="sm"
                        onClick={() => setCurrentPage(pageNum)}
                        className={`w-9 h-9 ${currentPage === pageNum ? 'bg-primary hover:bg-primary/90' : ''}`}
                      >
                        {pageNum}
                      </Button>
                    );
                  })}
                </div>
                <Button
                  variant="outline"
                  size="sm"
                  onClick={() => setCurrentPage(prev => Math.min(totalPages, prev + 1))}
                  disabled={currentPage === totalPages}
                >
                  {__('Next', 'surefeedback')}
                  <ChevronRight className="h-4 w-4 ml-1" />
                </Button>
              </div>
            </div>
          )}

          <div className="flex items-center justify-between border-t border-gray-200 pt-4 mt-6">
            <div className="flex items-center gap-2">
              {hasUnsavedChanges ? (
                <div className="flex items-center gap-2 text-sm text-amber-600 bg-amber-50 px-3 py-1.5 rounded-md border border-amber-200">
                  <AlertCircle className="h-4 w-4" />
                  <span className="font-medium">{__('Unsaved changes', 'surefeedback')}</span>
                </div>
              ) : (
                <div className="flex items-center gap-2 text-sm text-gray-600">
                  <CheckCircle2 className="h-4 w-4 text-green-600" />
                  <span>{__('All changes saved', 'surefeedback')}</span>
                </div>
              )}
            </div>
            <div className="flex gap-3">
              <Button
                variant="outline"
                onClick={loadPagesSettings}
                disabled={saving || !hasUnsavedChanges}
              >
                {__('Cancel', 'surefeedback')}
              </Button>
              <Button
                onClick={saveSettings}
                disabled={saving || !hasUnsavedChanges}
                className="bg-primary hover:bg-primary/90"
              >
                {saving ? (
                  <>
                    <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                    {__('Saving...', 'surefeedback')}
                  </>
                ) : (
                  __('Save Changes', 'surefeedback')
                )}
              </Button>
            </div>
          </div>
        </CardContent>
      </Card>

      <Toaster
        position="bottom-right"
        toastOptions={{
          className: 'surefeedback-toast',
          duration: 3000,
        }}
      />

      <AlertDialog open={showEnableAllDialog} onOpenChange={setShowEnableAllDialog}>
              <AlertDialogContent>
                <AlertDialogHeader>
                  <AlertDialogTitle>{__('Enable Widget for All Pages?', 'surefeedback')}</AlertDialogTitle>
                  <AlertDialogDescription>
                    {__('This will enable the SureFeedback widget on all pages of your website. This action can be undone.', 'surefeedback')}
                  </AlertDialogDescription>
                </AlertDialogHeader>
                <AlertDialogFooter>
                  <AlertDialogCancel disabled={saving}>{__('Cancel', 'surefeedback')}</AlertDialogCancel>
                  <AlertDialogAction onClick={confirmEnableAll} disabled={saving}>
                    {saving ? (
                      <>
                        <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                        {__('Enabling...', 'surefeedback')}
                      </>
                    ) : (
                      __('Enable All', 'surefeedback')
                    )}
                  </AlertDialogAction>
                </AlertDialogFooter>
              </AlertDialogContent>
            </AlertDialog>

      <AlertDialog open={showDisableAllDialog} onOpenChange={setShowDisableAllDialog}>
              <AlertDialogContent>
                <AlertDialogHeader>
                  <AlertDialogTitle>{__('Disable Widget for All Pages?', 'surefeedback')}</AlertDialogTitle>
                  <AlertDialogDescription>
                    {__('This will disable the SureFeedback widget on all pages of your website. Users will not be able to leave feedback. This action can be undone.', 'surefeedback')}
                  </AlertDialogDescription>
                </AlertDialogHeader>
                <AlertDialogFooter>
                  <AlertDialogCancel disabled={saving}>{__('Cancel', 'surefeedback')}</AlertDialogCancel>
                  <AlertDialogAction onClick={confirmDisableAll} disabled={saving} className="bg-red-600 hover:bg-red-700">
                    {saving ? (
                      <>
                        <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                        {__('Disabling...', 'surefeedback')}
                      </>
                    ) : (
                      __('Disable All', 'surefeedback')
                    )}
                  </AlertDialogAction>
                </AlertDialogFooter>
              </AlertDialogContent>
            </AlertDialog>
    </div>
  );
};

export default WidgetControl;
